.youtube-search-results__item {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    justify-items: left;
}

.youtube-search-results__item > h1 {
    order: 2;
}

.youtube-search-results__item > .picture {
    order: 1;
    display: block;
    width: 128px;
    height: 128px;
    flex: 0 0 auto;

    overflow: hidden;
}

.youtube-search-results__item > .picture > img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}